import { Nav, Tab, Tabs } from "react-bootstrap"
import { Link } from "react-router-dom"
import BlanceEnquiry from "./blanceEnquiry/BlanceEnquiry"
import CaseWithdrow from "./caseWithdrow/CaseWithdrow"
import MiniStatement from "./miniStatement/MiniStatement"
import AadhaarPay from "./aadhaarPay/AadhaarPay"
import { useEffect, useState } from "react"
import { IoHome } from "react-icons/io5";
import { ToastContainer } from "react-toastify"
import { Alert } from "antd"


function CompleteOnBoarding({
    balanceEnquiryPermission,
    cashWithdrawalPermission,
    miniStatementPermission,
    aadhaarPayPermission,
    walletShowHeader,
    isInstantpayOnBoarded,
    apiAepsType
  }) {
    console.log('CompleteOnBoarding Props:', {
        balanceEnquiryPermission,
        cashWithdrawalPermission,
        miniStatementPermission,
        aadhaarPayPermission
      });
    const [active, setActive] = useState(true)

    const [activeKey , setActiveKey] = useState()
    useEffect(() => {
        if (aadhaarPayPermission) setActiveKey("Aadhaar Pay");
        if (miniStatementPermission) setActiveKey("Mini Statement");
        if (cashWithdrawalPermission) setActiveKey("Cash Withdrawal");
        if (balanceEnquiryPermission) setActiveKey("Balance Enquiry");
      }, [
        aadhaarPayPermission,
        miniStatementPermission,
        cashWithdrawalPermission,
        balanceEnquiryPermission
      ]);
    console.log(active);
    return (
        <>

{(aadhaarPayPermission ||
  miniStatementPermission ||
  cashWithdrawalPermission ||
  balanceEnquiryPermission) ? 
            <div className="ContentArea">
                <div className="card">
                    <div className="card-header"><span>AEPS Services</span></div>
                    <div className="row p-4">
                        <div className="col-lg-12">
                            <div className="aeps-tab-section">
                                <Tabs
                                    // defaultActiveKey="Balance Enquiry"
                                    activeKey={activeKey}
                                    id="uncontrolled-tab-example"
                                    className="mb-3"
                                    onSelect={(k) => {
                                        setActiveKey(k);
                                        setActive(false);
                                      }}
                                >
                                       {balanceEnquiryPermission && (
                    <Tab eventKey="Balance Enquiry" title="Balance Enquiry " onChange={() => { setActive(false) }}>
                      <BlanceEnquiry active={active} apiAepsType={apiAepsType} walletShowHeader={walletShowHeader} />
                    </Tab>
                  )}
                  {cashWithdrawalPermission && 
               <Tab eventKey="Cash Withdrawal" title="Cash Withdrawal" onChange={() => { setActive(false) }}>
  <CaseWithdrow active={active} walletShowHeader={walletShowHeader} apiAepsType={apiAepsType} />
</Tab> }

                  {miniStatementPermission && (
                    <Tab eventKey="Mini Statement" title="Mini Statement" onChange={() => { setActive(false) }}>
                      <MiniStatement apiAepsType={apiAepsType} />
                    </Tab>  
                  )}
                  {aadhaarPayPermission && (
                  <Tab eventKey="Aadhaar Pay" title="Aadhaar Pay" onChange={() => { setActive(false) }} >
  <AadhaarPay apiAepsType={apiAepsType} walletShowHeader={walletShowHeader}/>
</Tab>
)}
                </Tabs>
                            </div>
                        </div>
                        {/* <div className="col-lg-3">
                            <Link to="/balance-enquiry" className="linkSection1">
                                <div className="linkSection">
                                    Balance Enquiry
                                </div>
                            </Link>
                        </div>
                        <div className="col-lg-3">
                            <Link to="/cash-withdrawal" className="linkSection1">
                                <div className="linkSection">
                                    Cash Withdrawal
                                </div>
                            </Link>
                        </div>
                        <div className="col-lg-3">
                            <Link to="/mini-statement" className="linkSection1">
                                <div className="linkSection">
                                    Mini Statement
                                </div>
                            </Link>
                        </div>
                        <div className="col-lg-3">
                            <Link to="/aadhaar-pay" className="linkSection1">
                                <div className="linkSection">
                                    Aadhaar Pay
                                </div>
                            </Link>
                        </div> */}
                    </div>
                </div>

            </div >: 
                <Alert message="Services is Disable by Admin" type="warning" showIcon /> }
        </>
    )
}
export default CompleteOnBoarding